import { Command } from '@ali/4ever-cangjie';
import toolbar from './toolbar';
import menubar from './menubar';
import increaseIndent from './commands/increaseIndent';
import decreaseIndent from './commands/decreaseIndent';
import onAction from './handlers/onAction';
import onKeyDown from './handlers/onKeyDown';
import { DEPRECATED_ACTIONS, actions } from './actions';
import { BiPlugin } from '../../types';
import createRenderToolbar from './createRenderToolbar';

const commands: Record<string, Command> = {
  increaseIndent,
  decreaseIndent,
};

function createIndentPlugin(config): BiPlugin {
  const indentConfig = config.indent || {};
  return {
    toolbar: toolbar(indentConfig),
    renderToolbar: createRenderToolbar(indentConfig.locale),
    menubar: menubar(indentConfig),
    commands,
    actions,
    onAction,
    onKeyDown,
    models: [],
  };
}

createIndentPlugin.actions = DEPRECATED_ACTIONS;
createIndentPlugin.commands = commands;

export default createIndentPlugin;
